.rgbKinethicSlider
	display: block
	position: relative
	overflow: hidden
	width: 100%
	height: 100vh

.rgbKinethicSlider canvas
	position: absolute
	display: block
	top: 0 !important
	left: 0 !important
	transform: scale(1) !important

nav a
	color: #fff
	text-decoration: none
	position: absolute
	top: 50vh
	z-index: 1
	padding: 9px 27px
	font-family: poppins
	font-size: 17px
	border-bottom: 2px solid #fff
	overflow: hidden
	transition: .5s
	&:hover
		color: #fff
nav a.next
	right: 20px
nav a.prev
	left: 20px